@import url('https://fonts.googleapis.com/css2?family=Abel&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abel&family=Cabin:ital,wght@1,500&display=swap');

body {
  font-family: 'Abel', Geneva, Verdana, sans-serif;
  font-size: 18px;
}

button {
  padding: 12px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  /*background: rgb(67,6,108);
  background: linear-gradient(90deg, rgba(67,6,108,1) 0%, rgba(255,61,144,1) 100%);*/
  background: rgba(34,193,195,1);
  /*background: linear-gradient(0deg, rgba(255,229,172,1) 0%, rgba(34,193,195,1) 100%);*/
  color: white;
  border-radius: 8px;
  cursor: pointer;
  border: none;
}

h1 {
  font-size: 3em;
  margin-bottom: 0;
  color: #82005d;
}
h3, h4 {
  margin-bottom: 0;
  font-family: 'Cabin', sans-serif;
}
h3.subtitle {
  text-decoration: underline;
}

nav {
  border-bottom: 1px solid #ccc;
  text-align: left;
  z-index: 100;
  width: 100%;
  position: fixed;
  top: 0;
  background: white;
  padding-left: 12.5%;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  background:#85d6d7;
  color: #fff;
}

#page-body {
  max-width: 75%;
  margin: auto;
  padding-top: 64px;
}

img {
  width: 100%;
 }

.article-image {
  width : 100%;
  margin-top: 20px;
}

.thumbnail {
   max-width: 200px;
   display: inline-block;
   margin-top: 15px;
   width: 25%;
 }

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 20px 0;
  border-bottom: 1px solid #aaa;
}
.article-list-item h3{
  margin-top: 40px;
}
.article-list-item .article-sample{
    display: inline-block;
    position: relative;
    margin: auto;
    vertical-align: top;
    width: 75%;
    padding-left: 20px;
}
.article-list-item p {
  padding-bottom: 32px;
  /*border-bottom: 1px solid black;*/
}
/*article list in articles page*/
#container-db-scroll .article-list-item {
  border-bottom: none;
}
#container-db-scroll .article-list-item .article-sample {
  display: block;
  width: 100%;
  padding-left: 0px;
}
#container-db-scroll .article-list-item p {
padding-bottom: 32px;

}
#container-db-scroll .thumbnail {
  max-width: 80%;
  display: block;
  width: 100%;
}

#add-comment-form {
  border-bottom: 1px solid #aaa;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 1px solid #aaa;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 1px solid black;
  padding: 16px;
}

#container-db-scroll{
  display: flex;
  height: 100vh;
  margin-bottom: -80px;
}

.main-content {
  max-width: 65%;
  padding-right: 5%;
  display: inline-block;
  overflow-y: scroll;
  flex: 1;
}

.sidebar-right {
  display: inline-block;
  position: relative;
  overflow: visible;
  max-width: 30%;
  vertical-align: top;
  overflow-y: scroll;
  flex: 2; 
}

#footer{
  height: 150px;
  width: 100%;
  padding: 50px 12.5%;
  background: #ececec;
  vertical-align: bottom;
  margin: 100px auto 0 auto;
  position: relative;
}

@media (max-width: 1080px) {
  #container-db-scroll {
    display:block;
    height: auto;
  }
  .main-content {
    max-width: 100%;
    display:block;
  }
  .sidebar-right {
    display: block;
    max-width: 100%;
  }
  .article-list-item{
    border-bottom: none;
    padding: 0 0;
  }
  .article-list-item p {
    border-bottom: 1px solid #aaa;
    padding-left: 0;
  }
  .article-list-item .article-sample {
    width: 100%;
    padding-left: 0px;
  }
}

